<template>
    <el-container class="container">
        <el-button
                type="info"
                :icon="icon"
                circle
                size="mini"
                :class="isCollapse?'float-button-collapse':'float-button'"
                @click="onCollapse">
        </el-button>
        <el-aside ref="aside" :style="{width: width + 'px'}">
            <nav-menu ref="navMenu"></nav-menu>
        </el-aside>
        <el-container style="padding: 0;">
            <el-header class="header">
                <div style="margin: auto; float: right; padding-top: 10px">
                    <span style="font-size: 20px; text-align: center;">用户ID：{{user.id}}</span>
                    <el-button type="submit" @click="handleSignOut" style="margin-left: 20px">登出</el-button>
                </div>
            </el-header>
            <el-main>
                <transition name="fade-transform" mode="out-in">
                    <router-view />
                </transition>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import navMenu from '../components/navMenu/navMenu';
    import { mapMutations, mapGetters } from "vuex";
    import G6Editor from '../components/editor/components/G6Editor'
    export default {
        name: "Main",
        data() {
            return {
                isCollapse: false,
                width: 201,
                icon: "el-icon-back",
            }
        },
        computed: {
            ...mapGetters({
                    user: "user"
                }
            )
        },
        components: {
            navMenu,
            // G6Editor
        },
        methods: {
            ...mapMutations({
                signOut: "signOut"
            }),
            handleSignOut() {
                this.signOut();
                this.$router.push("/");
            },
            onCollapse() {
                this.isCollapse = !this.isCollapse;
                this.$refs.navMenu.onCollapse();
                if(this.isCollapse){
                    setTimeout(() => {
                        this.width = 65;
                    }, 200)
                    this.icon = "el-icon-right";
                }else{
                    this.width = 201;
                    this.icon = "el-icon-back";
                }
            }
        }
    }
</script>

<style scoped>
    /* fade-transform */
    .fade-transform-leave-active,
    .fade-transform-enter-active {
        transition: all 0.5s;
    }

    .fade-transform-enter {
        opacity: 0;
        transform: translateX(-20px);
    }

    .fade-transform-leave-to {
        opacity: 0;
        transform: translateX(20px);
    }
    .float-button {
        z-index: 600;
        position: absolute;
        left: 50px;
        top: 50px;
        -webkit-animation: float-in 0.4s 1; /* Chrome, Safari, Opera */
        animation: float-in 0.4s 1;
        animation-fill-mode: forwards;
    }
    .float-button-in-animation {
        -webkit-animation: float-in 1s 1; /* Chrome, Safari, Opera */
        animation: float-in 1s 1;
    }
    .float-button-out-animation {
        -webkit-animation: float-out 1s 1; /* Chrome, Safari, Opera */
        animation: float-out 1s 1;
    }
    .float-button-collapse {
        z-index: 600;
        position: absolute;
        left: 186px;
        top: 50px;
        -webkit-animation: float-out 0.4s 1; /* Chrome, Safari, Opera */
        animation: float-out 0.4s 1;
        animation-fill-mode: forwards;
    }
    @-webkit-keyframes float-in {
        100% {left: 186px;}
    }
    @-webkit-keyframes float-out {
        100% {left: 50px;}
    }
    .nav {
        width: 201px;
    }
    .nav-collapse {
        width: 65px;
    }
    .header {
        border-bottom: 1px solid #e9e9e9;
    }
</style>
