<template>
    <div style="height: 100%">
        <el-menu
            :default-active="activeIndex"
            class="el-menu-vertical-demo"
            background-color="#2C2C38"
            text-color="#fff"
            :collapse="isCollapse"
            router>
            <div style="height: 100px;"></div>
            <el-menu-item index="/main">
                <i class="el-icon-s-platform"></i>
                <span slot="title">主页</span>
            </el-menu-item>
            <el-menu-item index="/main/flowList">
                <i class="el-icon-files"></i>
                <span slot="title">流程列表</span>
            </el-menu-item>
            <el-menu-item index="/main/formList">
                <i class="el-icon-collection"></i>
                <span slot="title">表单列表</span>
            </el-menu-item>
            <el-menu-item index="/main/userList">
                <i class="el-icon-collection"></i>
                <span slot="title">用户列表</span>
            </el-menu-item>
        </el-menu>
    </div>
</template>

<script>
    export default {
        name: "navMenu",
        data() {
            return {
                isCollapse: false,
                showLogo: true,
                animation: true,
            }
        },
        computed: {
            activeIndex() {
                // 对 graphEditor, formEditor 等进行处理 显示原本的路由 使得 navMenu 高亮生效
                if (this.$route.meta.activeMenu) {
                    return this.$route.meta.activeMenu;
                } else {
                    return this.$route.path;
                }
            },
        },
        props: {
            // isCollapse: {
            //     type: Boolean,
            //     default: false
            // }
        },
        methods: {
            onCollapse() {
                this.isCollapse = !this.isCollapse;
                this.showLogo = !this.showLogo;
            }
        }
    }
</script>

<style scoped>
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 100vh;
    }
    .el-menu--collapse {
        min-height: 100vh;
    }
</style>
